const Table = (props) => {
  console.log("Table");
  console.log(props);
  console.log("Table");
    const {characterData, removeCharacter} = props
  
    return (
      <table>
        <TableHeader />
        <TableBody characterData={characterData} removeCharacter={removeCharacter} />
      </table>
    )
  }


  export default Table 